<template>
	<header>
		<div class="head">
			<router-link to="/">
				<div class="back"><img src="../../static/images/arrow_left_b.png"></div>
			</router-link>
			<div class="search">
				<label><router-link to="/search"><input type="text" v-model="msg"/></router-link><router-link to="/Item"><button>搜索</button></router-link></label>
			</div>
			<div class="right" @click="isShow=!isShow"><img src="../../static/images/more_b.png"></div>
			<div class="san" v-show="isShow"></div>
			
			<div class="tishi" v-show="isShow">
				<div><img src="../../static/images/home_w.png"/><a href="#/">首页</a></div>
				<div><img src="../../static/images/cart_w.png"/><a href="#">购物车</a></div>
				<div><img src="../../static/images/member_w.png"/><a href="#">我的商城</a></div>
				<div><img src="../../static/images/message_w.png"/><a href="#">消息</a></div>
			</div>
		</div>
	</header>
	
</template>

<script>
	
	import Vue from 'vue'
	const Header=Vue.extend({
		name:'header',
		props:['msg'],
		data:function(){
			return {
				isShow:false
			}
		}
	})
	
	Vue.component('my-header',Header)
	export default Header
</script>

<style scoped lang="scss">
	header{
		display: flex;
		flex-direction: column;
		height: 10%;
		width: 100%;
	}
	.head{
		position: relative;
		width: 100%;
		height: 100%;
		background: #26a2ff;
		display: flex;
		justify-content: space-around;
		align-items: center;
		.back,.right{
			img{
				width: 16px;	
			}
		}
		.search{
			
			input{
				border: none;
				height: 25px;
			}
			button{
				border: none;
				height: 24px;
				
			}
		}
		.tishi{
			z-index: 10;
			border-radius: 4px;
			position: absolute;
			top: 100%;
			right: 0;
			width: 30%;
			height: 160px;
			display: flex;
			flex-direction:column ;
			justify-content: space-around;
			background: rgba(0,0,0,0.7);
			color: white;
			div{
				height: 40px;
				width: 100%;
				border-bottom: 1px solid white;
				padding-left: 10%;
				line-height: 40px;
				a{color: white;padding-left: 5px;font-size: 14px;}
				img{
					width: 16px;
				}
			}
			div:nth-child(4){
				border-bottom: none;
			}
		}
		.san{
				z-index: 10;
				position: absolute;
				top: 86%;
				right: 5%;
				height: 0;
				width: 0;
				border-bottom: 10px solid rgba(0,0,0,0.7);
				border-left: 10px solid transparent;
				border-right: 10px solid transparent;
			}
	}
</style>


