<template>
  <div class="banner-char">
  	
  	<div class="nctouch-main-box fixed-Width" id="main-container">
  		
  		<div class="nctouch-home-block" v-for="item in lists" :key="item.home1.data">
			<div class="item-pic">
				
				<router-link :to="{name:'bannerCondetails',params:{dataId:item.home1.data}}">
					<img :src="item.home1.image" alt="">
				
				</router-link>
			</div>
		</div>
		<!---->
		
	</div>

  </div>
</template>

<script>
	import Vue from 'vue'
	import axios from  'axios'
	const Bannerchar = Vue.extend({
		name:'Bannerchar',
		data () {
		    return {
		      lists:[]
		    }
		},
		//props:["isShow"],
		methods:{
		  	loadMore(){ 
//		  		var that = this;
//		  		$.ajax({
//					url:"http://www.hangowa.com/mo_bile/index.php?act=index&op=special&special_id=28",
//					
//					type:"post",
//					dataType:"jsonp",
//					success:function(data){
//						//console.log(data)
//						that.lists=data.datas.list;
//						
//						//console.log(data.datas)
//						//console.log(data.datas.list[0].home1.image)
//						//console.log(that.lists)
//					}
//					
//				})
		  	axios.get("https://bird.ioliu.cn/v1?url=http://www.hangowa.com/mo_bile/index.php?act=index&op=special&special_id=28")
		  		.then(function(data){
		  				//console.log(data);
		  				
		  				this.lists=data.data.datas.list;
		  		}.bind(this))
			}
	 	},
		mounted(){
			let that = this;
			setTimeout(()=>{
				this.loadMore() 
				//this.isShow = false;
			},1500)
			
		}
	})
	

	Vue.component('banner-char',Bannerchar)
	export default Bannerchar
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	
	.banner-char{
		margin-top: 60px;
		
		.nctouch-main-box {
		    display: block;
		    -margin-top: 2rem;
		    background-color: #f5f5f5;
		    
		    .nctouch-home-block {
			    width: 100%;
			    clear: both;
			    margin: 0;
			    background: #fff;
			    
			    .item-pic {
				    font-size: 0;
				    width: 100%;
				    margin: 0 auto;
				    
				    img {
					    width: 100%;
					    
					}
				    
				}
			    
			}
		    
		}
	}

/*
 <a :data-id="item.home1.data" href="#/bannerCondetails/:dataid">
 * 
