import Vue from 'vue'
import Router from 'vue-router'

import Outmai from '../components/Outmai'
import Finder from '../components/Finder'
import Order from '../components/Order'
import Mine from '../components/Mine'
import BannerCon from '../components/BannerCon'
import BannerCondetails from '../components/BannerCondetails'
import DetailsCons from '../components/DetailsCons'
import Comment from '../components/Comment'
import Login from '../components/Login'
import Register from '../components/Register'
import RegisterPhone from '../components/registercomponents/RegisterPhone'
import RegisterName from '../components/registercomponents/RegisterName'
import Aside from '../components/Aside'
import Search from '../components/Search'
import Item from '../components/Item'
import Shopcar from '../components/Shopcar'
Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',redirect:"/outmai"},
    {path: '*',redirect:"/outmai"},
    {path: '/outmai',name:'outmai',component:Outmai},
    {path: '/finder',name:'finder',component:Finder},
    {path: '/order',name:'order',component:Order},
    {path: '/mine',name:'mine',component:Mine},
    {path: '/bannercon',name:'bannercon',component:BannerCon},
    {path: '/bannerCondetails/:dataId',name:'bannerCondetails',component:BannerCondetails},
    {path: '/detailscons/:dataId',name:'detailscons',component:DetailsCons},
    {path: '/commentchar',name:'commentchar',component:Comment},
    {path: '/login',name:'login',component:Login},
    
    {path: '/aside',name:'aside',component:Aside},
    {path: '/search',name:'search',component:Search},
    {path: '/item',name:'item',component:Item},
    {path: '/shopcar',name:'shopcar',component:Shopcar},
      {
      path: '/register',
      name: 'register',
      component: Register,
      children:[
    {path: '/',redirect:"/registername"},
    {path: '*',redirect:"/registername"},
            {
      path: '/registerphone',
      name: 'registerphone',
      component: RegisterPhone}, 
    {
      path: '/registername',
      name: 'registername',
      component: RegisterName}          
     ]
    }
      
  ]
})


