<template>
	<div class="item">
		<header>
		<div class="head">
			<router-link to="/aside"><div class="back"><img src="../../static/images/arrow_left_b.png"></div></router-link>
			<div class="search">
				<label><input type="text" v-model="foodName"/><router-link to="/Item"><button @click="loadMore">搜索</button></router-link></label>
			</div>
			<div class="right" @click="isShow=!isShow"><img src="../../static/images/more_b.png"></div>
			<div class="san" v-show="isShow"></div>
			<div class="tishi" v-show="isShow">
				<div><img src="../../static/images/home_w.png"/><a href="#">首页</a></div>
				<div><img src="../../static/images/cart_w.png"/><a href="#">购物车</a></div>
				<div><img src="../../static/images/member_w.png"/><a href="#">我的商城</a></div>
				<div><img src="../../static/images/message_w.png"/><a href="#">消息</a></div>
			</div>
		</div>
		<div class="select">
			<div class="total">
				
				<ul v-show="show">
					<li @click="zonghe" :class="{'active':active1}">综合排序</li>
					<li @click="highToLow" :class="{'active':active2}">价格从高到低</li>
					<li @click="lowToHigh" :class="{'active':active3}">价格从低到高</li>
					<li @click="renqi" :class="{'active':active4}">人气排序</li>
				</ul>
				<div @click="hideShow" :class="{'active':active0}">{{msg}}<span>▽</span></div>
			</div>
			<div class="xianL" @click="xiaoliang" :class="{'active':active5}">销量排序</div>
			<div  class="shuaiX"><router-link to="/choose"><div>筛选</div></router-link></div>
			<div class="zongP" @click="flag=!flag"><img src="../../static/images/browse_list.png"/></div>
		</div>
		</header>
		
		<div class="item-list" v-if="!flag">
			<ul>
				<router-link v-for="(foods,index) in ven" :to="{ name:'bannerCondetails', params:{dataId:foods.goods_id}}" :key="index">
					<li>
						<div class="left"><img :src="foods.goods_image_url"></div>
						<div class="right">
							<h5>{{foods.goods_jingle}}</h5>
							<h6>{{foods.goods_name}}</h6>
							<div class="price"><span>¥</span>{{foods.goods_price}}</div>
							<div class="count">销量：<span>{{foods.goods_salenum}}</span></div>
						</div>
					</li>
				</router-link>
			</ul>
		</div>
		<div class="item-list-heng" v-if="flag">
			<ul>
				<router-link v-for="(foods,index) in ven" :to="{ name:'bannerCondetails', params: { dataId: foods.goods_id}}" :key="index">
					<li>
						<div class="top"><img :src="foods.goods_image_url"></div>
						<div class="buttom">
							<h6>{{foods.goods_name}}</h6>
							<div class="price"><span>¥</span>{{foods.goods_price}}</div>
							<div class="count">销量：<span>{{foods.goods_salenum}}</span></div>
						</div>
					</li>
				</router-link>
			</ul>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Header from './Headder'
	const Item=Vue.extend({
		name:'item',
		data:function(){
			return {
				foodName:'',
				isShow:false,//是否显示跳转小页面
				ven:null,
				list:10,
				key:null,
				order:null,
				msg:'综合排序',
				show:false,//‘综合排序’下面的选项是否显示
				flag:false,//是否显示商品竖着排的页面或横着排的页面
				active1:true,//添加active类名
				active2:false,
				active3:false,
				active4:false,
				active5:false,//由于不知道怎么在Vue里实现选项卡，只能用这种笨方法了
				active0:true
			}
		},
		 
		methods:{
			hideShow:function(){
				this.show=!this.show
				this.active0=true
			},
			zonghe:function(){
				this.key=null
				this.order=null
				this.msg='综合排序'
				this.loadMore()
				this.active1=true
				this.active2=false
				this.active3=false
				this.active4=false
				this.active5=false
			},
			highToLow:function(){
				this.key=3
				this.order=2
				this.msg='价格由高到低'
				this.loadMore()
				this.active1=false
				this.active2=true
				this.active3=false
				this.active4=false
				this.active5=false
			},
			lowToHigh:function(){
				this.key=3
				this.order=1
				this.msg='价格由低到高'
				this.loadMore()
				this.active1=false
				this.active2=false
				this.active3=true
				this.active4=false
				this.active5=false
			},
			renqi:function(){
				this.key=2
				this.order=2
				this.msg='人气排序'
				this.loadMore()
				this.active1=false
				this.active2=false
				this.active3=false
				this.active4=true
				this.active5=false
			},
			xiaoliang:function(){
				this.key=1
				this.order=2
				this.loadMore()
				this.active1=false
				this.active2=false
				this.active3=false
				this.active4=false
				this.active5=true
				this.active0=false
			},
			loadMore:function(){
				var that=this;
				
				this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=goods&op=goods_list&keyword='+this.foodName+'&keyword='+this.foodName,{
						page:that.list,
						key:that.key,
						order:that.order
					
				}).then(function(msg){
					console.log(msg);
					that.ven=msg.datas.goods_list;
				})
				
			}
				
		},
		mounted:function(){
			this.loadMore()
		}
	})
	Vue.component('item',Item)
	export default Item
</script>

<style scoped lang="scss">
	.active{
		color: red;
	}
	.item{
		height: 100%;
			header{
			display: flex;
			flex-direction: column;
			height: 15%;
			width: 100%;
		}
		header{
			position: fixed;
			top: 0;
			left: 0;
			.select{
				display: flex;
				justify-content: space-between;
				background: white;
				height: 60%;
				align-items: center;
				font-size: 14px;
				color: grey;
				.total{
					position: relative;
					width: 30%;
					div{
						padding-left: 20px;
					}
					ul{
						position: absolute;
						width: 400%;
						top: 26px;
						li{
							padding-left: 20px;
							line-height: 40px;
							background: white;
							border-bottom:1px solid gainsboro ;
						}
						li:nth-child(1){
							border-top:1px solid gainsboro ;
						}
					}
				}
				.zongP{
						img{
							width: 36%;
						}
					}
			}
		}
		.head{
			position: relative;
			width: 100%;
			height: 100%;
			background: #26a2ff;
			display: flex;
			justify-content: space-around;
			align-items: center;
			.back,.right{
				img{
				width: 16px;	
				}
			}
			.search{
				
				input{
					border: none;
					height: 25px;
				}
				button{
					border: none;
					height: 24px;
					
				}
			}
			.tishi{
				z-index: 10;
				border-radius: 4px;
				position: absolute;
				top: 100%;
				right: 0;
				width: 30%;
				height: 160px;
				display: flex;
				flex-direction:column ;
				justify-content: space-around;
				background: rgba(0,0,0,0.7);
				color: white;
				div{
					height: 40px;
					width: 100%;
					border-bottom: 1px solid white;
					padding-left: 10%;
					line-height: 40px;
					a{color: white;padding-left: 5px;font-size: 14px;}
					img{
						width: 16px;
					}
				}
			}
			.san{
					z-index: 10;
					position: absolute;
					top: 86%;
					right: 5%;
					height: 0;
					width: 0;
					border-bottom: 10px solid rgba(0,0,0,0.7);
					border-left: 10px solid transparent;
					border-right: 10px solid transparent;
				}
		}
	}
	.item-list{
		margin-top: 100px;
		ul{
			height: 100%;
			width: 100%;
			overflow: scroll;
			li{
				display: flex;
				width: 100%;
				height: 100px;
				padding-bottom: 8px;
				margin-top: 8px;
				border-bottom: 1px solid gainsboro;
				.left{
					width: 30%;
					height: 100%;
					img{
						width: 100%;
						height: 100%;
					}
				}
				.right{
					width: 65%;
					height: 100%;
					margin-left: 5px;
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					
					h5{
						font-weight: 100;
						font-size: 16px;
						white-space: nowrap;
						overflow:hidden;
						text-overflow: ellipsis;
					}
					h6{
						font-weight: 100;
						font-size: 12px;
						overflow:hidden;
						text-overflow: ellipsis;
					}
					.price{
						color: red;
						font-size: 16px;
						font-weight: 600;
					}
					.count{
						color: grey;
						font-size: 16px;
						span{
							color: black;
						}
					}
				}			
			}
		}	
	}
	
	
	.item-list-heng{
			margin-top: 100px;
		li{
			float: left;
			width: 48%;
			margin: 0 5px 10px 0;
			background: white;
			.top{
				img{
					width: 100%;
				}
			}
			.buttom{
				h6{
					width: 100%;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					padding: 0 10px;	
				}
				.price{
					color: red;
					padding: 10px 0 0 10px;
					font-weight: 600;
				}
				.count{
					padding: 10px 0 10px 10px;
					color: grey;
					font-size: 16px;
					span{
						color: black;
					}
				}
			}
		}
	}
</style>


