<template>
	<div class="nav-tab-d">
		<div class="nav-item">
			<div class="kefu"  @click="sendId">
				<img src="../../../static/images/kefu_b.png"/>
				<span>客服</span>
			</div>
			<div class="shopcar">
				<img src="../../../static/images/cart_b.png"/>
				<router-link to='/shopcar'><span>购物车</span></router-link>
			</div>
			<div class="buy" @click="alive">立即购买</div>
			<div class="toshopcar" @click="alive"><router-link to='/shopcar'>加入购物车</router-link></div>
		</div>
		<div class="gouwu">
				<div class="delete" @click="destory"></div>
				<div class="item">
					<div class="left">
						<img :src="img"/>
					</div>
					<div class="right">
						<li class="top">
							<span class="shopname">{{goodsName}}</span>
						</li>
						<li class="bottom">
							<div class="price"><span>¥</span>{{(price*num).toFixed(2)}}</div>
							<div class="count">库存：<span>{{number}}</span>件</div>
						</li>
					</div>
				</div>
				<div class="buy-count">
						<div class="g-num">购买数量</div>
						<div class="content">
							<button @click="reduceNum">-</button>
							<input type="text" value="1" v-model="num"/>
							<button @click="addNum">+</button>
						</div>
				</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	const NavTabD=Vue.extend({
		name:'navtabd',
		props:['id'],
		data:function(){
			return {
				img:null,
				goodsName:null,
				price:null,
				number:null,
				num:'1'
			}
		},
		
		methods:{
			sendId:function(){
				//this.$emit('childChange','id',this.id)
				this.$store.commit('getId',this.id)
				this.$store.commit('getNum',this.num)
				this.$store.commit('getPrice',this.price)
			},
			
			
			
			
			
			destory:function(){
				$('.gouwu').animate({top:80},1000)
			},
			alive:function(){
				$('.gouwu').animate({top:-250},1000)
			},
			addNum:function(){
				this.num++
			},
			reduceNum:function(){
				this.num--
				if(this.num<1){
					this.num=1;
				}
			}
		},
		mounted:function(){
			var that=this;
				
				this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=goods&op=goods_detail&key=&goods_id='+this.id)
				.then(function(msg){
					console.log(msg)
					that.img=msg.datas.goods_image.split(",")[0]
					that.goodsName=msg.datas.goods_info.goods_name
					that.price=msg.datas.goods_info.goods_price
					that.number=msg.datas.goods_info.goods_storage
				})
		},
		updated:function(){
			this.sendId()
		}
		
		
	})
	Vue.component('nav-tab-d',NavTabD)
	export default NavTabD
</script>

<style scoped lang="scss">
	.nav-tab-d{
		position: fixed;
		width: 100%;
		bottom: 0;
		left: 0;
		z-index: 100;
	}
	.nav-item{
		position: relative;
		z-index: 1000;
		width: 100%;
		height: 60px;
		display: flex;
		justify-content: space-between;
		font-size: 14px;
		.kefu{
			background: #ddd;
			width: 15%;
			border: 1px solid gainsboro;
			text-align:center;
			display:flex;
			flex-direction:column;
			justify-content:center;
			img{
				margin-left: 35%;
				width: 16px;
				display:block;
			}
			span{
					display: block;
					margin-top: 5px;
				}
		}
		.shopcar{
			background: #ddd;
			border-top: 1px solid gainsboro;
			border-bottom: 1px solid gainsboro;
			text-align:center;
			display:flex;
			flex-direction:column;
			justify-content:center;
			width: 20%;
			img{
				margin-left: 35%;
				width: 16px;
				display: block;
			}
			span{
					display: block;
					margin-top: 5px;
				}
		}
		.buy{
			width: 30%;
			text-align:center;
			display:flex;
			flex-direction:column;
			justify-content:center;
			background: #ED5564;
			color: white;
			font-weight: 600;
		}
		.toshopcar{
			width: 35%;
			background: #FB6E52;
			text-align:center;
			display:flex;
			flex-direction:column;
			justify-content:center;
			color: white;
			font-weight: 600;
		}
	}
	
	.gouwu{
		width: 100%;
		position: absolute;
		z-index: 999;
		top: 80px;
		left: 0;
		background: white;
		height: 250px;
		.delete{
			width: 20px;
			height: 20px;
			position: absolute;
			top: -10px;
			right: 5px;
			background-image: url(../../../static/images/close_window.png);
			background-size: 20px 20px;
		}
		
	}
	.item{
		padding-top: 6px;
		width: 100%;
		height: 100px;
		display: flex;
		justify-content: flex-start;
		
		#check{
			margin-top: 10%;
			margin-left: 10px;
		}
		.left{
			border-bottom: 1px solid gainsboro;
			width: 27%;
			height: 100px;
			text-align: center;
			img{
				width: 80%;
			}
		}
		.right{
			border-bottom: 1px solid gainsboro;
			width: 70%;
			display: flex;
			height: 100px;
			flex-direction: column;
			justify-content: space-around;
			.top{
				display: flex;
				justify-content: space-between;
				.shopname{
					font-size: 14px;
					margin: 5px;
					line-height: 20px;
				}
			}
			.bottom{
				display: flex;
				justify-content: space-between;
				.price{
					margin-left: 5px;
					color: red;
				}
				.count{
					color: #888;
				}
			}
		}
	}
	.buy-count{
			margin-top: 40px;
			width: 100%;
			height: 50px;
			border-top: 1px solid gainsboro;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.g-num{
				margin-left: 5px;
				color: grey;
			}
			.content{
					margin:  0 5px;
					display: flex;
					justify-content: center;
					input{
						width: 28px;
						text-align: center;
					}
			}
		}
</style>


