<template>
	<div class="bannerCondetails root-box">
		
		<div class="swiper-container">
	        <div class="swiper-wrapper" >
	            <div class="swiper-slide" v-for="item in charimg">
	           	<img :src='item' />
	            </div>
	        </div>
	    </div>
		
		<details-header></details-header>
		
		<details-center></details-center>
		
		<details-comment></details-comment>
		
		<div class="goods-detail-recom">
			<h4>店铺推荐</h4>
			<ul>
				
					<li v-for="item in goods_commend_list">
						<a>
							<router-link :to="{name:'bannerCondetails',params:{dataId:item.goods_id}}">
								<div class="pic"><img :src="item.goods_image_url"></div>
							</router-link>
							<dl>
								<dt>{{item.goods_name}}</dt>
								<dd>￥
									<em>{{item.goods_price}}</em>
								</dd>
							</dl>
							
						</a>
					</li>
				
			</ul>
		</div>
		
		<nav-tab-d :id="Id"></nav-tab-d>
		
		<div class="goods-detail-bottom">
			<a href="###" id="goodsBody1">
				<router-link :to="{name:'detailscons',params:{dataId:this.charid}}">点击查看商品详情</router-link>
			</a>
		</div>
		
		<div class="bannerCondetails"></div>
		  
	</div>
	 
</template>


<script>
	import NavTab from "./NavTab"
	
	import axios from 'axios'
	import Vue from 'vue'
	
	import NavTabD from "./DetailsComponents/NavTabD"
	import Detailsheader from "./DetailsComponents/Detailsheader"
	import DetailsCenter from "./DetailsComponents/DetailsCenter"
	import DetailsComment from "./DetailsComponents/DetailsComment"
	Vue.use(DetailsComment)
	export default {
	  name: 'BannerCondetails',
	  data () {
		    return {
		    	aa:null,
		    	bb:null,
		    	
		    	Id:this.$route.params.dataId,
		    	img:[],
		    	
		    	charid:null,
		    	charimg:null,
		    	goods_commend_list:"",
		    	
		    	goods_name:"",
		    	goods_price:"",
		    	goods_salenum:"",
		    	evaluation_count:"",
		    	evaluation_good_star:"",
		    	
		    	adress:"",
		    	adrecon:"",
		    	adstore:"",
		    	
		    	specimg:"",
		    	ddstorage:""
		    }
		},
		methods:{
		  	loadMore(){
//跨域  $route	  		  		
		  		
					axios.get("https://bird.ioliu.cn/v1?url=http://www.hangowa.com/mo_bile/index.php?act=goods&op=goods_detail&goods_id="+this.$route.params.dataId+"&key=")
		  		.then(function(data){
		  				//console.log(data);
		  				this.charid = this.$route.params.dataId;
						
						this.aa = data.data.datas.goods_image;
						
						this.charimg = this.aa.split(",");
						this.goods_commend_list = data.data.datas.goods_commend_list;
						//console.log(this.goods_commend_list)
						
						this.adress = data.data.datas.goods_hair_info.area_name;
						this.adrecon = data.data.datas.goods_hair_info.content;
						this.adstore = data.data.datas.goods_hair_info.if_store_cn;
						
						this.goods_name = data.data.datas.goods_info.goods_name;
						this.goods_price = data.data.datas.goods_info.goods_price;
						this.goods_salenum = data.data.datas.goods_info.goods_salenum;
						this.evaluation_count = data.data.datas.goods_info.evaluation_count;
						this.evaluation_good_star = data.data.datas.goods_info.evaluation_good_star;
						
						this.specimg = data.data.datas.spec_image[0];
						this.ddstorage = data.data.datas.goods_info.goods_storage;
		  		}.bind(this))
		  		

			},
//		  	ready(){
//		  		console.log('dataId:'+this.$route.params.dataId)
//		  	},
		  	swiper(){
		  		this.swiper=new Swiper(".swiper-container",{
					    autoplay:2000,
					    autoplayDisableOnInteraction : false,
					    touchRatio:1,
					    observer:true,
		          		observeParents:true,
				})
		  	}
		  	
	 	},
	 	beforeCreate(){
	 		this.charid = this.$route.params.dataId;
	 		//console.log(this.charid)
	 	},
		mounted(){
			this.loadMore()
			//this.ready()
			this.swiper()
		},
		update(){
			this.loadMore()
		},
		components:{
　　　　　　 DetailsCenter,
			DetailsComment
　　　　}
		
	}
	


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	.swiper-slide img{
		width:100%;
	}
	.goods-detail-recom {
	    background: #FFF;
	    padding: 0 0 0 0.25rem;
	    margin: 0.25rem 0;
	    background-color: #FFF;
	    border: solid #EEE;
	    border-width: 0.05rem;
	    overflow: hidden;
	    
	    h4 {
		    display: block;
		    height: 0.9rem;
		    padding: 0.2rem 0;
		    font-size: 0.4rem;
		    color: #888;
		    line-height: 0.45rem;
		}
		ul{
			font-size: 0;
			li{
				display: inline-block;
				width:22%;
				margin:0.3% 0.1rem 0;
				a{
					display: block;
					.pic{
						display: block;
						width:2rem;
						height:2rem;
						border:0.05rem solid #eee;
						img{
							width: 100%;
							height:100%;
						}
					}
					dl{
						margin-top:0.2rem;
						dt{
							overflow: hidden;
							display: block;
							height:1rem;
							font-size: .2rem;
							line-height: .5rem;
							color:#777;
							margin-bottom: 0.1rem;
						}
						dd{
							display: block;
							font-size: .2rem;
							color:333;
							em{
								font-size:.3rem;
							}
						}
					}
				}
			}
		}
	    
	}
	
	.goods-detail-bottom {
	    margin: 0.5rem 0 1.5rem 0;
	    text-align: center;
	    a {
		    display: inline-block;
		    width: 100%;
		    height: 1rem;
		    margin: 0 auto;
		    font-size: 0.3rem;
		    line-height: .5rem;
		    color: #999;
		}
	}
	
	


</style>



