<template>
  <div class="swiper">
	<div class="swiper-container">
        <div class="swiper-wrapper" >
            <div class="swiper-slide" v-for="item in items">
           <img :src='item.image' />
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'finderswiper',
   
  data () {
    return {
     items:'',
     swiper:null
    }
  },
   methods:{
  	getSwiper(){ 
  		var that=this;

 	this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=index&op=special&special_id=1').then(function(msg){
 		that.items=msg.datas.list[0].adv_list.item;
 	})
  	}
  },
   mounted(){
   	this.getSwiper()
    this.swiper=new Swiper(".swiper-container",{
			    autoplay:2000,
			    autoplayDisableOnInteraction : false,
			    touchRatio:1,
			    observer:true,
          observeParents:true
         
		})
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

   .mint-header{
   	height:60px;
   }
   .mint-header-title{
   	font-size:20px;
   }
   .mintui-back{
   	color:white;
   }
    .swiper-wrapper{
    width:100%;
    }
   .swiper-wrapper .swiper-slide img{
   	width:100%;
   	
   }
  .swiper{
  	margin-top:60px ;
  }
</style>



