<template>
  <div class="char-show">

		<div class="nctouch-home-box">
			
			<div class="nctouch-home-goods item-goods">
				<div class="tit-bar">超值好货</div>
				<div class="desc">
					<span class="time">小编向您推荐以下商品</span>
				</div>
				<ul class="goods-list">
		
					<li v-for="item in lists">
						
						<router-link :to="{name:'bannerCondetails',params:{dataId:item.goods_id}}">
							<div class="goods-pic"><img :src="item.goods_image" style="display: inline;"></div>
							<dl class="goods-info">
								<dt class="goods-name" v-text="item.goods_name"></dt>
								<dd class="goods-price">￥<em v-text="item.goods_promotion_price"></em></dd>
							</dl>
						</router-link>
						
					</li>
				</ul>
			</div>
			
			
			<div class="nctouch-home-goods item-goods">
				<div class="tit-bar">休闲零食</div>
				<div class="desc">
					<span class="time">小编向您推荐以下商品</span>
				</div>
				<ul class="goods-list">
		
					<li v-for="item in lists2">
						<router-link :to="{name:'bannerCondetails',params:{dataId:item.goods_id}}">
							<div class="goods-pic"><img :src="item.goods_image" style="display: inline;"></div>
							<dl class="goods-info">
								<dt class="goods-name" v-text="item.goods_name"></dt>
								<dd class="goods-price">￥<em v-text="item.goods_promotion_price"></em></dd>
							</dl>
						</router-link>
					</li>
				</ul>
			</div>
			
			<div class="nctouch-home-goods item-goods">
				<div class="tit-bar">酒水饮料</div>
				<div class="desc">
					<span class="time">小编向您推荐以下商品</span>
				</div>
				<ul class="goods-list">
		
					<li v-for="item in lists3">
						<router-link :to="{name:'bannerCondetails',params:{dataId:item.goods_id}}">
							<div class="goods-pic"><img :src="item.goods_image" style="display: inline;"></div>
							<dl class="goods-info">
								<dt class="goods-name" v-text="item.goods_name"></dt>
								<dd class="goods-price">￥<em v-text="item.goods_promotion_price"></em></dd>
							</dl>
						</router-link>
					</li>
				</ul>
			</div>
			
			
			
			
		</div>	
			
  </div>
</template>

<script>
	import Vue from 'vue'
	import axios from  'axios'
	Vue.prototype.$http = axios;
	
	const Charshow = Vue.extend({
		name:'Charshow',
		data () {
		    return {
		      lists:'',
		      lists2:'',
		      lists3:''
		    }
		},
		methods:{
		  	loadMore(){ 
//		  		var that = this;
//		  		$.ajax({
//					url:"http://www.hangowa.com/mo_bile/index.php?act=index",
//					type:"get",
//					dataType:"jsonp",
//					success:function(data){
//						//console.log(data)
//						that.lists=data.datas.list[1].goods.item;
//						that.lists2=data.datas.list[2].goods.item;
//						that.lists3=data.datas.list[3].goods.item;
//						//console.log(data.datas.list[2].goods)
//					}
//					
//				})
		  		
		  	axios.get("https://bird.ioliu.cn/v1?url=http://www.hangowa.com/mo_bile/index.php?act=index")
		  		.then(function(data){
		  				
						//console.log(data)
						this.lists=data.data.datas.list[1].goods.item;
						this.lists2=data.data.datas.list[2].goods.item;
						this.lists3=data.data.datas.list[3].goods.item;
						
						
		  		}.bind(this))
		  	
		  		
			}
	 	},
		mounted(){
			this.loadMore() 	
		}
				 
	})
	

	Vue.component('char-show',Charshow)
	export default Charshow
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
	body {
	    background-color: #F0F0F0;
	}
	.nctouch-home-box {
	    display: block;
	    max-width: 640px;
	    margin: 0 auto;
	    overflow: hidden;
	    position: relative;
	    z-index: 0;
	    
	    .item-goods {
		    background-color: #fff;
		    
		    .tit-bar {
			    font-size: 0.4rem;
			    font-weight: normal;
			    padding: 0.5rem 0 0;
			    text-align: center;
			    line-height: 0.2rem;
			    border-left: 0;
			    color: #333;
			    height: 0.4rem;
    			margin: 0.3rem 0 0.8rem 0.1rem;
			}
			
			.desc {
			    font-size: 0.3rem;
			    font-weight: normal;
			    text-align: center;
			    line-height: 0.5rem;
			    border-left: 0;
			    color: #333;
			    
			    .time {
				    color: #999;
				    display: inline-block;
				    position: relative;
				}
			    
			}
			
			ul.goods-list {
			    font-size: 0;
			    margin-bottom: 0.5rem;
			    
			    li {
				    background-color: #FFF;
				    vertical-align: top;
				    display: inline-block;
				    width: 47%;
				    overflow: hidden;
				    border-radius: 0.2rem;
				    
				    a {
					    display: block;
					    
					    .goods-pic {
						    width: 100%;
						    img {
							    width: 100%;
							}
						}
						
						dl {
						    padding: 0.2rem;  
						    .goods-name {
						        display: block;
						        font-size: 0.35rem;
						        line-height: 0.45rem;
						        color: #000;
						        height: 0.9rem;
						        overflow: hidden;
						    }
						    
						}
					}
									    
				}
			   	li:nth-child(odd) {
				    margin: 0.5rem 1% 0 2%;
				}
				li:nth-child(even) {
				    margin: 0.5rem 2% 0 1%;
				}
			}
			
		}
		
		nctouch-home-goods{
		    width: 100%;
		    clear: both;
		    margin: 0;
		    background: #fff;
		}
	    
	}
</style>



