<template>
	<div>
		<div class="line"></div>
	<div class="form-box">
       	<span>手机</span> <input type="text"  placeholder="请输入手机号" id="txtMobile"  />
       </div>
       <div class="line"></div>
       <div class="form-box">
       	<span>密码</span> <input type="password"  placeholder="请输入密码" id="txtpassword"  />
       </div>
       <div class="line"></div>
	<div class="msg-box">
	<span class="left">验证码</span> <input type="text" placeholder="请输入验证码" id="textnum"/>
		<span class="right">
			
		</span>
	</div>
	<div class="line"></div>
	<br />
	<mt-button type="danger" size="large" id="btnSubmit">注册</mt-button>
  </div>
</template>

<script>
	export default{
		name:'registerphone',
		data(){
			return {
		    
			}
},
mounted() {
	var k1=false;
	var k2=false;
	var k3=false;
	$("#txtMobile").blur(function(){
		var reg=/^1[3568]\d{9}$/;
		var str=$("#txtMobile").val();
		if(!reg.test(str)){
			alert("手机号码不正确")
			return ;
		}
		else{
			k1=true;
		}
	})
	
	$("#txtpassword").blur(function(){
		var reg=/^\w{6,20}$/;
		
		var str=$("#txtpassword").val();
		if(!reg.test(str)){
			alert("密码不符合规范")
			return ;
		}
		else{
			k2=true;
		}
	})
	
	$("#textnum").blur(function(){
		
		var str=$('.right').text().toLowerCase();
		var str1=$(this).val().toLowerCase();
		if(str==str1){
			k3=true;
		}
		else{
			alert("请重新输入验证码")
		}
	})
	
			$("#btnSubmit").click(function(){
    	if(k1&&k2&&k3){
    	 var deff = $.ajax({
			type:"get",
			url:"http://datainfo.duapp.com/shopdata/userinfo.php",
			data:{
				status:"register",
				userID:$("#txtMobile").val(),
				password:$("#txtpassword").val()
			}
		});
		deff.done(function(msg){  //成功
			switch(msg){
				case "0" : alert("用户名重复了");break;
				case "1" : alert("注册成功了，3秒后将跳转到登录进行登录");
							setTimeout(function(){
								location.href = "#/login";
							},3000)
						   break;
				case "2" : alert("服务器产生错误，稍后再试");break;
			}
		})
		deff.error(function(){ //失败
			alert("请求失败了");
		})
    	}
    	else{
    		alert("请完善信息");
    	}
    })
	
	function rand(min, max) {
		return Math.floor(Math.random() * (max - min + 1)) + min
	}
	num();
	
	$(".right").click(function() {
		num();
	})

	function num() {
		var str = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
		var a = str.charAt(rand(0, 61)) + str.charAt(rand(0, 61)) + str.charAt(rand(0, 61)) + str.charAt(rand(0, 61));
		$(".right").text(a);
	}
}
}</script>

<style scoped lang="scss">
a{
	text-decoration: none;
}
 .msg-box{
 	width:100%;
 	height:1.22rem;
 	display: flex;
 	align-items: center;
 	input{
 		height:.6rem;
 		border:none;
 		outline:none;
 		font-size:.42rem;
 		margin-left:.32rem;
 		width:4rem;
 	}
 	.left{
 		display: inline-block;
 		width:2.6rem;
 		height:1.22rem;
 		font-size: .42rem;
 		line-height:1.28rem;
 		text-indent: 10px;
 	}
 	.right{
 		display: inline-block;
 		width:2.4rem;
 		height:.8rem;
 		margin-left:10px ;
 	    font-size:.53rem;
 	    text-align: center;
 	    line-height: .8rem;
 	    color:orangered;
 	}
 }
 
 .form-box{
 	font-size: .42rem;
 	width:100%;
 	height:1.3rem;
 	display: flex;
 	span{
 		display: inline-block;
 		width:30%;
 		height:1.3rem;
 		line-height: 1.3rem;
 		text-indent: 10px;
 	}
 	input{
 	     border: none ;
 	     outline: none;
 	}
 }
 
 .line{
 	width:98%;
 	height:1px;
 	margin-left:2% ;
 	/*float: right;*/
 	border-top:1px solid #DDDDDD;
 }
</style>


