<template>
	<div class="left-item">
		<ul>
			<li v-for="(data1,index1) in dataRou1" :key='index1'>
				<h5><span></span>{{data1.cat_name}}</h5>
				<div class="type-item">
					<p v-for="(data2,index2) in dataRou1[index1].child" :key='index2'>{{data2.cat_name}}</p>
				</div>
			</li>
			
		</ul>
		
	</div>
</template>

<script>
	import Vue from 'vue'
	const Asideleft = Vue.extend({
        name:'asideleft',
        props:['dataRou1','dataRou2']
    })
	Vue.component('aside-left',Asideleft)
	export default Asideleft
</script>

<style scoped lang="scss">
	.left-item{
			display: none;
			height: 100%;
			width: 100%;
			h5{
				display: flex;
				width: 100%;
				height: 40px;
				line-height: 40px;
				align-items: center;
				background: ghostwhite;
				span{
					display: block;
					height: 10px;
					width: 10px;
					border-radius:50% ;
					background: gold;
					margin: 0 10px;
				}
			}
			.type-item{
				width: 100%;
				display: flex;
				justify-content: flex-start;
				background: white;
				flex-wrap: wrap;
				p{
					width: 30%;
					padding: 20px 10px;
					font-size: 12px;
				}
			}
		}
		.left-item:nth-child(1){
			display: block;
		}
</style>


