	<footer id="footer">
		<div class="nctouch-footer-wrap posr">
			<div class="nav-text">
				<a href="#/login">登录</a>
				<a href="#/register">注册</a>
				<a href="http://www.hangowa.com/wap/tmpl/member/login.html">反馈</a>
				<a href="javascript:;" class="gotop" @click="scroll('0',300)">返回顶部</a>
			</div>
			<div class="nav-pic">
				<a href="http://www.hangowa.com/shop/index.php?act=mb_app" class="app">
					<span>
						<i></i>
					</span>
					<p>客户端</p>
				</a>
				<a href="javascript:;" class="touch"><span>
					<i></i>
				</span>
				<p>触屏版</p>
				</a>
				<a href="http://www.hangowa.com/shop" class="pc">
					<span>
						<i></i>
					</span>
					<p>电脑版</p>
				</a>
			</div>
			<div class="copyright">Copyright&nbsp;©&nbsp;2014-2016 汉购网
