				}
			}
			
			.copyright {
			    height: 0.8rem;
			    font-size: 0.25rem;
			    line-height:0.8rem;
			    color: #555;
			    text-align: center;
			    text-shadow: 1px 1px 0 rgba(255,255,255,1);
			    margin-bottom: 1rem;
			}
		    
		}
	    
	}
</style>

