<template>
	<div class="shopcar">
		<div class="title">
			<router-link  :to="{ name:'detail', params: { id: Id}}"><span><img src="../../static/images/arrow_left_b.png"></span></router-link>
			<!--../../../static/images/arrow_left_b.png-->
			<span>购物车</span>
			<span><img src="../../static/images/more_b.png"></span>
			<!--../../../static/images/more_b.png-->
		</div>
		<div class="msg">
			<input type="checkbox" />
			<p>全选</p>
		</div>
		<div class="item" v-if="isRemove">
			<input type="checkbox" id="check"/>
			<div class="left">
				<img :src="img"/>
			</div>
			<div class="right">
				<li class="top">
					<span class="shopname">{{goodsName}}</span>
					<span class="delete" @click="remove"><img src="../../static/images/del_b.png"></span>
					<!--../../../static/images/del_b.png-->
				</li>
				<li class="bottom">
					<div class="price"><span>¥</span>{{(price*num).toFixed(2)}}</div>
					<div class="content">
						<button @click="reduceNum">-</button>
						<input type="text" value="1" v-model="num"/>
						<button @click="addNum">+</button>
					</div>
				</li>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	const Shopcar = Vue.extend({
        name:'shopcar',
        data:function(){
        	return {
        		img:null,
				goodsName:null,
				isRemove:true
//				price:null
//	      		id:null,
//        		num:1
        	}
        },
        computed:{
        	Id:function(){	
        		return this.$store.state.module.stateId
        	},
        	num:function(){
        		return this.$store.state.module.stateNum
        	},
        	price:function(){
        		return this.$store.state.module.statePrice
        	}
        },
        methods:{
        	remove:function(){
        		if(confirm('确定要删除吗')){
        			this.isRemove=false
        		}
    
        	},
        	addNum:function(){
				this.$store.state.module.stateNum++
			},
			reduceNum:function(){
				this.$store.state.module.stateNum--
				if(this.$store.state.module.stateNum<1){
					this.$store.state.module.stateNum=1;
				}
			}
        },
        mounted:function(){
        	var that=this;
				
				this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=goods&op=goods_detail&key=&goods_id='+that.Id)
				.then(function(msg){
					that.img=msg.datas.goods_image.split(",")[0]
					that.goodsName=msg.datas.goods_info.goods_name
					that.price=msg.datas.goods_info.goods_price
				})
        }
    })
	Vue.component('shopcar',Shopcar)
	export default Shopcar
</script>

<style scoped lang="scss">
	.title{
		height: 60px;
		background: gainsboro;
		display: flex;
		justify-content: space-between;
		align-items: center;
		span{
			display: block;
			font-size: 20px;
			margin: 0 10px;
			img{
				width: 16px;
			}
		}
	}
	.msg{
		width: 100%;
		display: flex;
		justify-content: flex-start;
		height: 50px;
		border-bottom:1px solid gainsboro ;
		align-items: center;
		input{
			margin-left: 10px;
		}
		p{
			margin-left: 10px;
		}
	}
	.item{
		padding-top: 6px;
		width: 100%;
		height: 100px;
		display: flex;
		justify-content: flex-start;
		#check{
			margin-top: 10%;
			margin-left: 10px;
		}
		.left{
			width: 27%;
			height: 100%;
			img{
				width: 100%;
			}
		}
		.right{
			width: 70%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			.top{
				display: flex;
				justify-content: space-between;
				.shopname{
					font-size: 14px;
					margin: 5px;
				}
				.delete{
					margin: 5px;
					img{
						width: 16px;
					}
				}
			}
			.bottom{
				display: flex;
				justify-content: space-between;
				input{
					width: 28px;
				}
				.content{
					margin:  0 5px;
					display: flex;
					justify-content: center;
				}
				.price{
					margin: 0 5px;
					color: red;
				}
			}
		}
	}
</style>


