<template>
	<footer class="nav-tab">
		
		<router-link to="/outmai">
			<span class="iconfont">&#xe60a;</span>
			首页
		</router-link>
		
		<router-link to="/finder">
			<span class="iconfont">&#xe61b;</span>
			发现
		</router-link>
		
		<router-link to="/aside">
			<span class="iconfont">&#xe639;</span>
			类目
		</router-link>
		
		<router-link to="/mine">
			<span class="iconfont">&#xe67b;</span>
			我的
		</router-link>
	</footer>
</template>

<script>
	import Vue from 'vue'
	const NavTab = Vue.extend({
		name:'navtab'
	})
	Vue.component('nav-tab',NavTab)
	export default NavTab
	
</script>

<style scoped lang="scss">
	.nav-tab{
		z-index: 99;
		position: fixed;
		bottom: 0;left: 0;width: 100%;
	    padding-top: .1rem ;
	    padding-bottom: .1rem ;
	    background: rgba(85,85,85,0.95);;
	    height: 1.5rem;
	    box-shadow: 0 -0.026667rem 0.053333rem rgba(0,0,0,.1);
	    display: flex;
	    
	    a{
	    	flex-grow: 1;
	    	display: flex;
	    	flex-flow: column;
	    	align-items: center;
	    	justify-content: space-around;
	    	font-size: .3rem;
	    	color: #fff;
	    	.iconfont{
	    		font-size: .6rem;
	    	}
	    	&.router-link-active{
	    		color: #0089dc;
	    	}
	    }
	    
	}
</style>


