import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
Vue.use(Vuex)

const state={
	module:{
		stateNum:'',
		stateId:'',
		stateMsg:'',
		statePrice:''
	},
	cart:[]
}

const mutations={
	getId:function(state,id){
		state.module.stateId=id
	},
	getNum:function(state,num){
		state.module.stateNum=num
	},
	putItem:function(state,cartInfo){
		state.cart.push(cartInfo)
	},
	getMsg:function(state,msg){
		state.module.stateMsg=msg
	},
	getPrice:function(state,price){
		state.module.statePrice=price
	}
}

const store = new Vuex.Store({
	state,
	actions,
	mutations
})


export default store



