<template>
	<div class="commentheader">
		
		<header id="header" class="posf">
			  	<div class="header-wrap">
				    <div class="header-l"> 
				    	<a href="javascript:history.go(-1)">
				    		<i class="back"></i> 
				    	</a> 
				    </div>
				    
				    <ul class="header-nav">
				      <li v-for='(data,key,index) in datas' v-on:click="addClassFun(index)" v-bind:class='{cur:index==qwerqwre}'>
				      		
			      		<router-link to="">
			      		{{data.data}}
			      		</router-link>
				      	
				      </li>

				    </ul>
				    
				    <div class="header-r"> 
				    	<a id="header-nav" @click="isShow=!isShow">
				    		<i class="more"></i>
				    		<sup></sup>
				    	</a> 
				    </div>
			  	</div>
			  	
				<div class="tishi" v-show="isShow">
					<!--搜索-->
					<div><img src="../../../static/images/home_w.png"/><a href="#/">首页</a></div>
					<div><img src="../../../static/images/cart_w.png"/><a href="#">购物车</a></div>
					<div><img src="../../../static/images/member_w.png"/><a href="#">我的商城</a></div>
					<div><img src="../../../static/images/message_w.png"/><a href="#">消息</a></div>
				</div>
				
			</header>
		
	</div>
	
	
</template>

<script>
	import Vue from 'vue'
	import axios from  'axios'
	const Commentheader = Vue.extend({
		name:'commentheader',
		data(){
			return{
				isShow:false,
				datas: {  
					data1: {  
						data: "评论",  
						ifAdd: 0 
					}
				},  
				qwerqwre:"0" 
			}
		},
		methods:{
			addClassFun: function(index) {  
				this.qwerqwre = index;  
				  
			}  
		},
		mounted(){
			
		}
		
	})
	

	Vue.component('comment-header',Commentheader)
	export default Commentheader
	
</script>

<style scoped lang="scss">
		.tishi{
			z-index: 10;
			border-radius: 4px;
			position: absolute;
			top: 100%;
			right: 0;
			width: 30%;
			height: 160px;
			display: flex;
			flex-direction:column ;
			justify-content: space-around;
			background: rgba(0,0,0,0.7);
			color: white;
			div{
				height: 40px;
				width: 100%;
				border-bottom: 1px solid white;
				padding-left: 10%;
				line-height: 40px;
				a{color: white;padding-left: 5px;font-size: 14px;}
				img{
					width: 16px;
				}
			}
			div:nth-child(4){
				border-bottom: none;
			}
		}
		header {
		-display: none;
	    background: #F8F8F8;
	    height: 1.2rem;
	    -position: absolute;
	    position: fixed;
	    z-index: 8;
	    top: 0;
	    left: 0;
	    right: 0;
	    bottom: auto;
	    -border-bottom: solid 0.05rem #EEE;
	    background-color: transparent;
	}
	.header-wrap {
        display: block;
	    max-width: 640px;
	    margin: 0 auto;
	    text-align: center;
	}
	.header-l{
	    display: block;
	    width: .65rem;
	    height: .65rem;
	    position: absolute;
	    top: 0;
	    left: 0;
    }
   .header-l a,.header-r a{
    	display: inline-block;
    	width: 0.65rem;
	    height: 0.65rem;
	    margin: 0.2775rem;
	    padding: 0;
	    background: rgba(255,255,255,0.75);
	    border: solid 0.05rem #EEE;
	    border-radius: 100%;
    }
    .header-l a i.back {
    	display: block;
	    width: 60%;
	    height: 60%;
	    margin: 20%;
	    background-image: url(../../../static/images/arrow_left_r.png);
	    background-repeat: no-repeat;
	    background-position: 50% 50%;
	    background-size: 100%;
	    opacity: 1;
	}
	.transparent .header-title, .transparent .header-nav {
	    display: none;
	}
	.header-r {
	    position: absolute;
	    z-index: 1;
	    top: 0;
	    right: 0;
	    font-size: 0;
	    text-align: right;
	}
	.header-r a i.more {
		display: block;
	    width: 60%;
	    height: 60%;
	    margin: 20%;
	    background-image: url(../../../static/images/more_r.png);
	    background-repeat: no-repeat;
	    background-position: 50% 50%;
	    background-size: 100%;
	    opacity: 1;
	}
	/*2*/
	header.posf {
	    animation: headerFixed 0.5s ease-in-out;
	    -webkit-animation: headerFixed 0.5s ease-in-out;
	}
	.posf {
	    position: fixed;
	    background: #fff;
	    .header-nav {
		    display: inline-block;
		    margin: 0 auto;
		    vertical-align: top;
		    font-size: 0;
		    white-space: nowrap;
		    li {
			    display: inline-block;
			    vertical-align: top;
			    text-align: center;
			    height: 0.65rem;
			    a {
				    display: inline-block;
				    padding: 0.05rem 0.25rem;
				    margin: 0 0.25rem;
				    font-size: 0.4rem;
				    line-height: 1rem;
				    color: #333;
				}
			}
		}
	}
	li.cur a {
	    padding: 0.1rem 0.05rem 0 0.05rem;
	    color: #ED5564!important;
	    -border-bottom: solid 0.1rem #ED5564;
	    font-size: 0.4rem;
	    font-weight: 600;
	}
	
</style>


