<template>
	<div>
		<div class="line"></div>
       <div class="form-box">
       	<span>用户名</span> <input type="text"  placeholder="输入6到20位用户名" id="txtMobile"  />
       </div>
       	<div class="line"></div>
       	<div class="form-box">
       	<span>输入密码</span> <input type="password" placeholder="输入6到20位密码" id="txtPassword"/>
       </div>
       <div class="line"></div>
       <div class="form-box">
       	<span>确认密码</span> <input type="password"  placeholder="请再次输入密码" id="tpassword"/>
       </div>
       <div class="line"></div>
       <div class="form-box">
       	<span>邮箱</span> <input placeholder="请输入邮箱" type="email" id="txtemail"/>
       </div>
        <div class="line"></div>
        <br />
         <mt-button type="danger" size="large" id="btnSubmit">注册</mt-button>
	</div>
</template>

<script>
	import Vue from 'vue'
	import { Field,Button } from 'mint-ui'
    Vue.component(Field.name, Field)
    Vue.component(Button.name, Button)

	export default{
		name:'registername',
		data () {
			return {
             
			}
		},
		mounted () {
			//表单验证 
	//手机号码验证
	var k1=false;
	var k2=false;
	var k3=false;
	var k4=false;
	
	$("#txtMobile").blur(function(){
		var reg=/^\w{6,20}$/;
		var str=$("#txtMobile").val();
		if(!reg.test(str)){
			alert("用户名不符合规范")
		
		}
		else{
			k1=true;
		}
	})
	
	//两次输入密码验证
	$("#tpassword").blur(function(){
		if(!($("#tpassword").val()==$("#txtPassword").val())){
			
			alert("两次密码输入不一致")
		}
		else{
			k4=true;
		}
	})
  //邮箱验证
	$("#txtemail").blur(function(){
		var reg=/^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/;
		var str=$("#txtemail").val();
		if(!reg.test(str)){
			alert("邮箱不正确")
		
		}
		else{
			k3=true;
		}
	})	
		
	//密码验证
	$("#txtPassword").blur(function(){
		var reg=/^\w{6,18}$/;
		var str=$("#txtPassword").val();
		if(!reg.test(str)){
			alert('密码不符合规范')
		
		}
		else{
			
			k2=true;
		}
	})
		//注册
		$("#btnSubmit").click(function(){
    	if(k1&&k2&&k3&&k4){
    	 var deff = $.ajax({
			type:"get",
			url:"http://datainfo.duapp.com/shopdata/userinfo.php",
			data:{
				status:"register",
				userID:$("#txtMobile").val(),
				password:$("#txtPassword").val()
			}
		});
		deff.done(function(msg){  //成功
			switch(msg){
				case "0" : alert("用户名重复了");break;
				case "1" : alert("注册成功了，3秒后将跳转到登录进行登录");
							setTimeout(function(){
								location.href = "#/login";
							},3000)
						   break;
				case "2" : alert("服务器产生错误，稍后再试");break;
			}
		})
		deff.error(function(){ //失败
			alert("请求失败了");
		})
    	}
    	else{
    		alert("请完善信息");
    	}
    })
	
		}
	}

</script>

<style scoped="scoped" lang="scss">
a{
	text-decoration: none;
}
 .line{
 	width:98%;
 	height:1px;
 	margin-left:2% ;
 	/*float: right;*/
 	border-top:1px solid #DDDDDD;
 }
 .form-box{
 	font-size:.45rem;
 	width:100%;
 	height:1.3rem;
 	display: flex;
 	span{
 		display: inline-block;
 		width:30%;
 		height:1.3rem;
 		line-height: 1.3rem;
 		text-indent: 10px;
 	}
 	input{
 	     border: none ;
 	     outline: none;
 	}
 }
</style>


