<template>
	<aside>
		<my-header></my-header>
		<div class="bottom-item">
			<div class="aside">
					<ul class="top">
						<li  class="first" v-for="(food,index) in foods" @click="isShow(index)">
							<h5 class="iconfont">&#xe63b;</h5>
							<p>{{food}}</p>
						</li>
					</ul>
				<div class="bottom"></div>
			</div>
			<div class="aside-left">
				<aside-left :dataRou1='ven1' :dataRou2='ven2' v-for="(num,index) in 5" :key="index"></aside-left>
			</div>
		</div>
	</aside>	
</template>

<script>
	import Vue from 'vue'
	import Header from './Headder'
	import axios from 'axios'
	
	import Asideleft from './fentype/Aside-left'
	const Aside=Vue.extend({
		name:'aside',
		data:function(){
			return {
				type:[],
				typeAll:[],
				tId:null,
				typeMiddle:[],
				ven1:null,
				ven2:null,
				foods:['生鲜水果','蛋奶素食','休闲零食','粮油杂货','酒水饮品']
			}
		},
		methods:{
			isShow:function(count){
				$('.first').click(function(){
					$(this).addClass('active').siblings().removeClass('active');
					$('.aside-left').find('.left-item').eq($(this).index()).show().siblings().hide()
				})
				var that=this
				this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=goods_class').then(function(msg){
					var len1=msg.datas.category_list[count].child.length;
						that.ven1=msg.datas.category_list[count].child
						for(let i=0;i<len1;i++){
							that.ven2=that.ven1[i].child;
						}
				})
			}
		},
		mounted:function(){
			var that=this
			
			this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=goods_class').then(function(msg){
				var len1=msg.datas.category_list[0].child.length;
					that.ven1=msg.datas.category_list[0].child
					for(let i=0;i<len1;i++){
						that.ven2=that.ven1[i].child;
					}
			})
		}
	})
	Vue.component('my-aside',Aside)
	export default Aside
</script>

<style scoped lang="scss">
	aside{
	height: 100%;
	.bottom-item{
		height: 90%;
		display: flex;
		width: 100%;
	}
}
	.aside{
		display: flex;
		height: 100%;
		background: gainsboro;
		width: 25%;
		flex-direction: column;
		justify-content: space-between;
		.top{
			display: flex;
			height: 100%;
			width: 80%;
			flex-direction: column;
			margin-left:10%;
			color:gray;
			.first{
				width: 100%;
				height: 20%;
				text-align:center;
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				h5{
					font-size: 30px;
					margin: 10px 0;
					font-weight: 100;
				}
				P{
					border-bottom: 2px solid gainsboro;
					width: auto;
					padding-bottom: 10px;
				}
			}
			.active{
					color: red;
					p{
						border-bottom-color: red;
					}
				}
		}
		.bottom{
			height: 50%;
			width: 100%;
		}
	}
	.aside-left{
		width: 75%;
		height: 100%;
		overflow: scroll;
	}
	
</style>


