<template>
	<div class="goods">
		<div class="title-box">
			{{title}}
		</div>
		<div class="info-box">
			<div class="xx-box" v-for="info in items">
				<img :src="info.image" />
			</div>

		</div>

		<div class="min-box">
			<div class="small-box" v-for="info in somegood">
				<router-link :to="{name:'bannerCondetails',params:{dataId:info.data}}">
				<img :src="info.image" />
				</router-link>
			</div>

		</div>

		<div class="stitle-box">
			{{stitle}}
		</div>

		<div class="good-list">
			<ul>
				<li v-for="info in selectgood">
					
					<router-link :to="{name:'bannerCondetails',params:{dataId:info.goods_id}}">
					<img :src="info.goods_image" />
					</router-link>
					
					<p>{{info.goods_name}}</p>

					<span>¥{{info.goods_promotion_price}}</span>
				</li>
			</ul>
		</div>
		
		<div class="stitle-box">
			{{sstitle}}
		</div>
		
		<div class="good-list">
			<ul>
				<li v-for="info in allgood">
					<router-link :to="{name:'bannerCondetails',params:{dataId:info.goods_id}}">
					<img :src="info.goods_image" />
					</router-link>
					<p>{{info.goods_name}}</p>

					<span>¥{{info.goods_promotion_price}}</span>
				</li>
			</ul>
		</div>
		

	</div>
</template>

<script>
	export default {
		name: 'finderBody',

		data() {
			return {
				items: '',
				title: '',
				somegood: '',
				stitle: '',
				sstitle:'',
				selectgood: '',
				allgood:'',
				swiper: null
			}
		},
		methods: {
			getMsg() {
				var that = this;
					this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=index&op=special&special_id=1').then(function(msg){
			console.log(msg)
 			that.items = msg.datas.list[1].explode3.item;
			that.title = msg.datas.list[1].explode3.title;
			that.somegood = msg.datas.list[2].home3.item;
			that.stitle = msg.datas.list[3].goods.title;
			that.selectgood = msg.datas.list[3].goods.item;
			that.sstitle = msg.datas.list[4].goods.title;
			that.allgood=msg.datas.list[4].goods.item;
 	})
  	}

			
		},
		mounted() {
			this.getMsg()

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
		margin: 0;
	}
	
	p {
		margin: 0;
		padding: 0;
	}
	
	a {
		color: #42b983;
	}
	

	.swiper-wrapper img {
		width: 100%;
	}
	
	.title-box {
		width: 100%;
		height:1.1rem;
		line-height: 1.1rem;
		text-indent: 10px;
		font-size:.5rem
	}
	
	.info-box {
		width: 100%;
		display: flex;
		.xx-box{
			height:1.4rem;
		}
	}
	
	.info-box img {
		width: 100%;
	}
	
	.min-box {
		width: 100%;
		display: flex;
	}
	
	.min-box img {
		width: 100%;
	}
	.stitle-box {
		text-align: center;
		height:1.1rem;
		line-height: 1.1rem;
		text-indent: 10px;
		font-size:.5rem
	}
	
	ul {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
	}
	
	ul li {
		width: 50%;
	}
	
	 li img {
		margin-left: 10%;
		margin-bottom: 5px;
		width: 80%;
	}
	
	p {
		font-size: 0.27rem;
		text-align: left;
		margin: 0 10%;
		height: 0.9rem;
		overflow: hidden;
	}
	
	span {
		font-size:.5rem;
		display: block;
		padding: 10px 0;
		margin-left: 10%;
		color: #DB4453;
	}
	
</style>


