<template>
	<div class="search">
		<my-header :msg="value"></my-header>
		<div class="search-item">
			<h4>热门搜索</h4>
			<ul>
				<li v-for="food in list" @click="searchFood(food)">{{food}}</li>
			</ul>
			<h4>历史搜索</h4>
			<ul>
				<li v-for="his_food in his_list">{{his_food}}</li>
			</ul>
			<span @click="clear">清空历史记录</span>
		</div>
		<nav-tab></nav-tab>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Header from './Headder'
	import NavTab from './NavTab'
	const Search=Vue.extend({
		name:'search',
		data:function(){
			return {
				list:[],
				his_list:[],
				value:null
			}
		},
		methods:{
			searchFood:function(foods){
				this.his_list.push(foods)
				this.value=foods
			},
			clear:function(){
				this.his_list=[]
			}
		},
		mounted:function(){
			var that=this;
			this.$jsonp('http://www.hangowa.com/mo_bile/index.php?act=index&op=search_key_list')
			.then(function(msg){
				//console.log(msg)
				var len=msg.datas.list.length
					var ven=msg.datas.list
					for(var i=0;i<len;i++){
						that.list.push(ven[i])
					}
			})
		}
	})
	Vue.component('search',Search)
	export default Search
</script>

<style scoped lang="scss">
	.search{
		height: 100%;
	}
	.search-item{
		padding-top: 20px;
		background: ghostwhite;
		width: 100%;
		height: 100%;
		h4{
			font-weight: 100;
			color: grey;
			margin-left: 10px;
		}
		span{
			display: block;
			border: 1px solid gainsboro;
			color: grey;
			padding: 5px;
			border-radius: 15px;
			width: 40%;
			text-align: center;
			margin-left: 25%;
			margin-top: 30px;
		}
		ul{
			display: flex;
			justify-content: flex-start;
			width: 100%;
			flex-wrap: wrap;
			li{
				width: 15%;
				border: 1px solid gainsboro;
				border-radius: 15px;
				text-align: center;
				padding: 5px;
				font-size: 12px;
				color: gray;
				margin: 10px;
			}
		}
		
	}
</style>


