<template>
<div>
  <login-header> </login-header>
  <mt-field label="账户" placeholder="请输入用户名" v-model="username"></mt-field>
  <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>

 <div class="btn-box">
 	<mt-button type="danger" @click="sub" size="large">登录</mt-button>
 </div>
   <h2>
   	<span>合作账号登录</span>
   </h2>
   <p>
   	   <span class="iconfont icon-qq  pqq"></span>
   </p>
</div>
</template>

<script>
	import Vue from 'vue'
	import MintUI from 'mint-ui'
	import 'mint-ui/lib/style.css'
	Vue.use(MintUI)
	import LoginHeader from './logincomponents/LoginHeader'
	import { Field,Button } from 'mint-ui'
    Vue.component(Field.name, Field)
    Vue.component(Button.name, Button)
	export default {
  name: 'login',
  data () {
    return {
     username:'',
     password:''
     
    }
  },
  components:{
  	LoginHeader
  },
  methods:{
  	
  	sub:function(){
  		if(this.username==''||this.password==''){
    		alert("用户名密码不能为空")
    		
    	}
    	else{
    		 var deff = $.ajax({
			type:"get",
			url:"http://datainfo.duapp.com/shopdata/userinfo.php",
			data:{
				status:"login",
				userID:this.username,
				password:this.password
			}
		});
		deff.done(function(msg){  //成功
			switch(msg){
				case "0" : alert("用户名不存在");break;
				case "2" : alert("用户名与密码不符");
						   break;
				default: alert("登录成功");break;
			}
		})
		deff.error(function(){ //失败
			alert("请求失败了");
		})
    	}
    	
    }
  	
  },
  mounted(){
     
}
 }
</script>

<style scoped lang="scss">
	  a{
	  	text-decoration: none;
	  }
	.btn-box{
		margin:10px 0;
	}
	h2{
		position: relative;
		height:0.8rem;
		width:100%;
		border-bottom: 1px gainsboro solid;
		span{
			position: absolute;
			left:37%;
			bottom: -10px;;
			display: block;
			height:0.53rem;
			width:2.6rem;
			font-size: 0.4rem;
			line-height: 0.53rem;
			text-align: center;
			background: #ffffff;
		}
	}
	p{
		width:100%;
		height:2.13rem;
		line-height: 2.13rem;
		text-align: center;
	}
	.pqq{
		font-size:1.3rem;
		color:#4fc0e8;
	
	}
	
</style>


