<template>
<div>
	<register-header></register-header>
	<div class="info-box" >
		 <a href="#/registername" id="btn-tab"  class="active">
		 	<router-link to="./login">
		 	普通注册
		 	</router-link>
		 </a>
		 <a href="#/registerphone" id="btn-tab1">手机注册</a>
	</div>
  <router-view></router-view>
</div>
</template>

<script>
	import Vue from 'vue'
	import RegisterHeader from './registercomponents/RegisterHeader'
	export default {
  name: 'register',
  data () {
    return {
     isshow:true
    }
  },
  components:{
  	RegisterHeader
  },
  mounted(){
  	$("#btn-tab").on('click',function(){
  		$(this).addClass('active').siblings().removeClass('active')
  	})
  	$("#btn-tab1").on('click',function(){
  		$(this).addClass('active').siblings().removeClass('active')
  	})
 }
}
</script>

<style scoped lang="scss">
	  a{
	  	text-decoration: none;
	  }
	.info-box{
		margin-bottom:0.8rem ;
		width:100%;
		height:1.1rem;
		display: flex;
		a{
			display: inline-block;
			width:50%;
			height:100%;
			color:#333;
			font-size:0.4rem;
			text-align: center;
			line-height: 1.1rem;
		}
	}
	.active{
		border-bottom:solid 1px red ;
		a{
			color:red
		}
	}
</style>


