<template>
  <div class="finder-header">
  	
	<header id="header" class="fixed fixed-Width">
	  <div class="header-wrap">
	    <div class="header-l"> <a href="javascript:history.go(-1)"> <i class="back"></i> </a> </div>
	    <div class="header-title">
	      <h1>发现</h1>
	    </div>
	    <div class="header-r">
	    	<a id="header-nav" href="javascript:;">
	    		<i class="more" ></i>
	    	</a></div>
	  </div>
	 

	  
	</header>
	
  </div>
</template>

<script>
	import Vue from 'vue'
//	import axios from  'axios'
	const FinderHeader = Vue.extend({
		name:'finderheader',
		data (){
			return {
				
			}
		
		}
	})
	

	Vue.component('finder-header',FinderHeader)
	export default FinderHeader
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>

	#header {
	    position: fixed;
	    z-index: 2 !important;
	    top: 0;
	    bottom: auto;
	    left: 0;
	    right: 0;
	    background: #26a2ff;
   	 	padding: 10px;
   	 	
   	 	.header-wrap {
		    display: block;
		    max-width: 640px;
		    margin: 0 auto;
		    text-align: center;
		    width: 66.6%;
		    height: 40px;
		    line-height: 40px;
		    float: left;
		    
		    .header-l {
			    display: block;
			    width: .9rem;
			    height:0.9rem;
			    position: absolute;
			    top: 0;
			    left: 0;
			    
			    a {
				    position: relative;
				    z-index: 1;
				    display: inline-block;
				    width: .8rem;
				    height: 0.8rem;
				    padding: 0.5rem 0 0 .2rem;
				    color: white;
				    
				    i.back {
				    	display: block;
					    width: 80%;
					    height: 80%;
					    margin: 10%;
					    background-image: url(../../../static/images/arrow_left_b.png);
					    background-position: 50% 50%;
					    background-repeat: no-repeat;
					    background-size: 100%;
					    opacity: 0.75;
					}
				    
				}
			    
			}
			
			.header-title {
			    display: inline-block;
			    margin: 0 auto;
			    
			    h1 {
			    	color: white;
				    font-size: .5rem;
				    line-height: 0.9rem;
				    height: 0.9rem;
				}
			}
			
			.header-r {
				z-index: 99;
			    position: absolute;
			    top: 0;
			    right: 0;
			    font-size: 0;
			    text-align: right;
			    
			    a {
				    position: relative;
				    z-index: 1;
				    display: inline-block;
				    vertical-align: top;
				    width: 0.6rem;
				    height: .6rem;
				    margin: .4rem .4rem 0 0;
				    
					.more {
						display: inline-block;
						width:100%;
						height:100%;
					    background-image: url(../../../static/images/more_b.png);
					    background-repeat: no-repeat;
					    background-position: 50% 50%;
					    background-size: 100%;
					    opacity: 0.65;
					}
				    
				}
			    
			}
					    
		}
   	 	
	}

</style>



