<template>
	
	<div class="detailcenter">
		<vue-area :props-show="show" :props-result="result" v-on:result="areaResult"></vue-area>
		
		<div class="mask"></div>
		
		<div class="goods-detail-cnt">
			<div class="goods-detail-name">
				<dl> 
					<dt v-text="this.$parent.goods_name"></dt>
					<dd>【特惠包邮】</dd>
				</dl>
			</div>
			
			<div class="goods-detail-price">
				
					<dl>
						<dt>￥<em v-text="this.$parent.goods_price"></em></dt>
					</dl>
				
				<span class="sold">销量：{{this.$parent.goods_salenum}}件</span>
			</div>
			
			
			<div class="goods-detail-item">
				<div class="itme-name">送至</div>
				<div class="item-con">
					
					<a @click="show=!show" href="javascript:;" id="get_area_selected">
						<dl class="goods-detail-freight">
							<dt>
								<span id="get_area_selected_name">{{this.$parent.adress}}</span>
								<strong id="get_area_selected_whether">{{this.$parent.adstore}}</strong>
							</dt>
							<dd id="get_area_selected_content">{{this.$parent.adrecon}}</dd>
						</dl>
					</a>
					
				</div>
				<div class="item-more location"></div>
			</div>
			
			<!--<div class="goods-detail-item" id="goods_spec_selected">
				<div class="itme-name">已选</div>
				<div class="item-con">
					<dl class="goods-detail-sel">
						<dt>
							
							<span>默认</span>
							
						</dt>
					</dl>
				</div>
				<div class="item-more"></div>
			</div>-->

			<!--<div class="goods-detail-store">
				<a href="store.html?store_id=15">
					<div class="store-name"><i class="icon-store"></i>荆门众邦特色馆</div>
					<div class="store-rate">
						<span class="high">描述相符
							<em>4.7</em>
							<i>高</i>
						</span>
						<span class="high">服务态度
							<em>4.8</em>
							<i>高</i>
						</span>
						<span class="high">发货速度
							<em>4.8</em>
							<i>高</i>
						</span>
					</div>
					<div class="item-more"></div>
				</a>
			</div>-->
			
		</div>
		
		
	</div>
	
</template>

<script>
	
	import Vue from 'vue'
	import axios from  'axios'
	import vueArea from 'vue-area'
	
	const DetailsCenter = Vue.extend({
		name:'detailcenter',
		data(){
			return{
				result: null,
				show: false
			}
		},
		methods:{
			areaResult: function(show, result){
		        this.show = show
		        this.result = result
//		        window.addEventListener("click",function(event){  
//			        event.preventDefault(); //标准  
//			        //event.returnValue = false //IE          
//			        //return false; //用于处理使用对象属性注册的处理程序   
//			    },false); 
				if(this.show){
					$("body").css({"position":"fixed","overflow-y":"hidden"})
				}else if(this.show==false){
					$("body").css({"position":"relative","overflow-y":"scroll"})
				}
				
		    }
		},
		mounted(){
			
		},
		components:{
			vueArea
		}
	})
	

	Vue.component('details-center',DetailsCenter)
	export default DetailsCenter
	
</script>

<style scoped lang="scss">
	
	
	.goods-detail-name {
	    display: block;
	    padding: 0.25rem 0.25rem 0.1rem 0.25rem;
	    background-color: #FFF;
	    border-top: solid 0.05rem #EEE;
	    
	    dt {
		    display: block;
		    font-size: 0.4rem;
		    line-height: 0.4rem;
		}
		
	    dd {
		    margin-top: 0.15rem;
		    font-size: 0.25rem;
		    color: #777;
		    line-height: 0.69rem;
		}
	}
	
	.goods-detail-price {
	    position: relative;
	    z-index: 1;
	    display: block;
	    padding: 0 0.25rem;
	    background-color: #FFF;
	    
	    dl {
		    display: inline-block;
		    
		    dt {
			    color:#333;
			    font-size: 0.35rem;
			}
			dd {
			    font-size: 0.35rem;
			    color: #888;
			}
		}
		
		.sold {
		    position: absolute;
		    z-index: 1;
		    top: 0.15rem;
		    right: 0.75rem;
		    display: block;
		    font-size: 0.35rem;
		    line-height: 1rem;
		    height: 1rem;
		}
	}
	
	
	.goods-detail-item {
	    position: relative;
	    z-index: 1;
	    padding: 0.5rem;
	    background: #FFF;
	    border-bottom: solid 0.05rem #eee;
	    .itme-name {
		    position: absolute;
		    z-index: 1;
		    top: 0.5rem;
		    left: 0.25rem;
		    display: block;
		    width: 0.8rem;
		    height: 0.6rem;
		    font-size: 0.4rem;
		    color: #888;
		    line-height: 0.6rem;
		}
		.item-con {
		    display: block;
		    margin-left: 0.8rem;
		    dl{
		    	display: block;
    			line-height: 0.6rem;
    			dt{
    				font-size: 0.4rem;
    				color: #333;
    				strong {
					    color: #ED5564;
					    margin-left: 0.5rem;
					}
    			}
		    }
		}
		.item-more.location {
			position: absolute;
		    z-index: -1;
		    top: 0.8rem;
		    right: 0.25rem;
		    display: block;
		    width: 0.4rem;
		    height: 0.45rem;
		    background-repeat: no-repeat;
		    background-position: 50% 50%;
		    background-size: 80%;
		    background-image: url(../../../static/images/location_b.png);
		    opacity: 0.55;
		}
	}
</style>


